export default ".pods{margin-bottom:1em}.pods__header{font-size:16px;font-weight:500;display:flex;align-items:center}.pods__header__tags{margin-left:auto;display:flex}.pods__container{margin-top:10px;border:1px solid #8fa4b1;display:flex;align-items:center;border-radius:3px;background-color:#dee6eb;padding:7px;flex-wrap:wrap}.pods__container .pod-icon{margin:3px}.pods__container--dark{border-color:#30303d;background-color:#09090f}.pod-icon{width:30px;height:30px;line-height:30px;font-size:18px;text-align:center;border:1px solid #30303d;background-color:#22212d;color:#dad6e7;border-radius:3px;cursor:pointer}.pod-icon--success{background-color:#339440;color:#fff;border:1px solid #339440}.pod-icon--success:hover{background-color:#246b2d;border-color:#246b2d}.pod-icon--dark.pod-icon--success{background-color:#12221e;color:#3eb74f;border:1px solid #246b2d}.pod-icon--dark.pod-icon--success:hover{border-color:#3eb74f}.pod-icon--failure{background-color:#9e3d38;color:#fff;border:1px solid #9e3d38}.pod-icon--failure:hover{border-color:#f85149;background-color:#f85149}.pod-icon--dark.pod-icon--failure{background-color:#22141a;color:#f85149;border:1px solid #9e3d38}.pod-icon--dark.pod-icon--failure:hover{border-color:#f85149}.pod-icon--pending{background-color:#58a6fe;color:#fff;border:1px solid #58a6fe}.pod-icon--pending:hover{border-color:#3a5e88;background-color:#3a5e88}.pod-icon--dark.pod-icon--pending{background-color:#172636;color:#58a6fe;border:1px solid #3a5e88}.pod-icon--dark.pod-icon--pending:hover{border-color:#58a6fe}";