export default ".rollouts-list{display:flex;padding:20px;flex-wrap:wrap}.rollouts-list__widget{border-radius:5px;padding:17px;border:1px solid #ccd6dd;font-size:14px;margin-right:20px;box-shadow:1px 2px 2px rgba(0,0,0,.05);color:#495763;background-color:#fff;width:400px;height:max-content;flex-shrink:0;margin-bottom:1.5em}.rollouts-list__widget--dark{border-color:#30303d;box-shadow:1px 2px 3px 1px #09090f;color:#abb2b9;background:none}.rollouts-list__widget:hover,.rollouts-list__widget--selected{border-color:#f07548}.rollouts-list__widget--dark:hover,.rollouts-list__widget--dark.rollouts-list__widget--selected{border-color:#8b4329}.rollouts-list__widget__refresh:hover{color:#f07548}.rollouts-list__widget__body{margin-bottom:1em;padding-bottom:.75em;border-bottom:1px solid #ccd6dd}.rollouts-list__widget__body--dark{border-bottom:1px solid #30303d}.rollouts-list__widget header{color:#363c4a;display:flex;align-items:center;font-weight:500;font-size:18px;border-bottom:1px solid #ccd6dd;padding-bottom:1em;margin-bottom:1em}.rollouts-list__widget--dark header{color:#dad6e7;border-bottom:1px solid #30303d}.rollouts-list__widget__actions{display:flex;align-items:center;margin-top:1.5em}";